@import url(https://fonts.googleapis.com/css?family=Ultra&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;display:flex;flex-direction:column}.app-header{display:flex;flex-direction:row;justify-content:space-between}.app-header .app-logo{display:flex;flex-direction:row;align-items:center}.app-header .app-logo .app-logo-img{flex:0 0 100px}.app-header .app-logo .app-logo-name{font-family:'Ultra', serif;font-size:40px}.app-header .app-actions{padding:20px;flex:1 1;display:flex;justify-content:flex-end}.app-header .app-actions a{padding:20px}

