.app-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  .app-logo {
    display: flex;
    flex-direction: row;
    align-items: center;

    .app-logo-img {
      flex: 0 0 100px;
    }

    .app-logo-name {
      @import url('https://fonts.googleapis.com/css?family=Ultra&display=swap');
      font-family: 'Ultra', serif;
      font-size: 40px;
      //font-weight: bolder;
    }
  }

  .app-actions {
    padding: 20px;
    flex: 1;
    display: flex;
    justify-content: flex-end;
    a {
      padding: 20px;
    }
  }
}
